<template>
  <div class="coupon">
    <div class="navtop">
      <van-nav-bar
        title="优惠卷"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>
    <div class="img">
      <img src="../assets/image/couponEmpty.png">
      <p>暂无优惠卷可用</p>
    </div>
  </div>
</template>

<script>
export default {
  name:"coupon",
  methods: {
    onClickLeft() {
      this.$router.back()
    },
  },
  created(){
  //获取token
    let tokenString = this.$cookies.get("tokenString");

    console.log(tokenString);

    //判断token存不存在
    if (!tokenString) {
      console.log("浏览器token不存在");
      return this.$router.push({ name: "Login" });
    }
  }
}
</script>
<style lang="less" scoped>
.coupon{
  /deep/.navtop{
    .van-icon{
      color: #ff8600;
    }
    .van-nav-bar__title{
      color: #ff8600;
    }
  }
  .img{
    text-align: center;
    p{
      color: #666;
    }
  }
}
</style>
